import React, { useMemo } from "react"
import Nav from "react-bootstrap/Nav"
import { Container } from "react-bootstrap"
import { useStaticQuery, graphql } from "gatsby"
import Img from "gatsby-image"

function Image(props) {
  const data = useStaticQuery(graphql`
    query {
      allFile(filter: { internal: { mediaType: { regex: "images/" } } }) {
        edges {
          node {
            relativePath
            childImageSharp {
              fluid(maxWidth: 300, quality: 100) {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  `)
  const match = useMemo(
    () =>
      data.allFile.edges.find(({ node }) => props.src === node.relativePath),
    [data, props.src]
  )
  return (
    <div className="d-flex justify-content-center mt-2">
      <div
        style={{
          width: `80%`,
          backgroundColor: `white`,
          marginBottom: `25px`,
          border: `2px solid #000`,
          borderRadius: `4px`,
        }}
      >
        <Img
          style={{
            width: `100%`,
            borderBottom: `1px solid #000`,
            maxWidth: `100%`,
            maxHeight: `auto`,
            objectFit: `contain`,
          }}
          fluid={match.node.childImageSharp.fluid}
        />
        <Container
          style={{
            textAlign: `center`,
            padding: `10px 10px`,
          }}
        >
          <p
            style={{
              font: `normal normal 90% Merriweather`,
            }}
            className="text-justify"
          >
            <i>Fig.</i> {props.alt}
          </p>
        </Container>
      </div>
    </div>
  )
}

function TOC(props) {
  return (
    <div style={{ margin: 0 }} className="sticky-top">
      <Nav className="navbar navbar-dark bg-dark rounded">
        <h1
          className="navbar-brand mb-0"
          style={{
            font: `normal bold 150% Fira Code`,
            textDecoration: `underline`,
          }}
        >
          CONTENT
        </h1>
        <Nav className="flex-column">
          {props.tableOfContents.map(heading => (
            <Nav.Link
              href={"#" + heading.value.replace(/\s+/g, "-").toLowerCase()}
              key={heading.value}
              style={{
                marginLeft: heading.depth - 2 + `rem`,
                font: `normal bold 130% Comic Neue`,
                color: `white`,
              }}
            >
              - {heading.value}
            </Nav.Link>
          ))}
        </Nav>
      </Nav>
    </div>
  )
}

export { Image, TOC }
